*,
*::after,
*::before {
  margin: 0;
  box-sizing: inherit;
}

body {
  box-sizing: border-box;
}

@font-face {
  font-family: 'OpenSans';
  font-style: normal;
  font-weight: 300;
  src: url('~assets/fonts/normal/OpenSans-Light.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: normal;
  font-weight: 400;
  src: url('~assets/fonts/normal/OpenSans-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: normal;
  font-weight: 500;
  src: url('~assets/fonts/normal/OpenSans-Medium.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: normal;
  font-weight: 600;
  src: url('~assets/fonts/normal/OpenSans-SemiBold.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: normal;
  font-weight: 700;
  src: url('~assets/fonts/normal/OpenSans-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: italic;
  font-weight: 300;
  src: url('~assets/fonts/italic/OpenSans-LightItalic.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: italic;
  font-weight: 400;
