}

@font-face {
  font-family: 'OpenSans';
  font-style: italic;
  font-weight: 500;
  src: url('~assets/fonts/italic/OpenSans-MediumItalic.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: italic;
  font-weight: 600;
  src: url('~assets/fonts/italic/OpenSans-SemiBoldItalic.ttf') format('truetype');
}

@font-face {
  font-family: 'OpenSans';
  font-style: italic;
  font-weight: 700;
  src: url('~assets/fonts/italic/OpenSans-BoldItalic.ttf') format('truetype');
